import { HttpService, Store } from '../base';
const Connection = new HttpService();

const Config = {
	// Server API
	apiUrl: '',
	baseUrl: process.env.NODE_ENV === 'production' ? '' : 'http://localhost:3002',
};

const EndPoint = {
	// EndPoints
	API_DOC_JSON: Config.baseUrl + Config.apiUrl + '/ApiDoc/api',
	API_DOC_URL: Config.baseUrl + Config.apiUrl + '/ApiDoc/',
};

const getReqData = (reqData) => {
	let reqObj = {
		channel: 'ApiDoc',
		appVersion: '1.0.0',

		langCd: Store.getItem('langCd') || 'en',
		userId: Store.getItem('userId'),
		deviceId: Store.getItem('deviceId'),
		params: reqData,
		device: {},
	};

	return reqObj;
};

export { Config, Connection, EndPoint, getReqData };
